.main__section {
  width: 100vw;
  height: 624px;
  margin: 376 0px;
  padding-top: 30px;
  padding-bottom: 60px;
  background-color: rgb(239, 249, 255);
  @media all and(min-width: 768px) {
    height: 397px;
  }
}
.main__section--grid {
  display: grid;
  grid-template-columns: 180px;
  grid-template-rows: 138px 138px 138px;
  align-items: center;
  justify-content: center;
  @media all and(min-width: 768px) {
    display: grid;
    grid-template-columns: 180px 180px 180px;
    grid-template-rows: 138px;
    align-items: center;
    justify-content: space-around;
  }
}

.main__section--title {
  font-family: "RobotoSlab-Bold";
  font-weight: 700;
  font-size: 24px;
  color: #000;
  margin-top: 10px;
  margin-bottom: 45px;
  text-align: center;
  white-space: nowrap;
  margin-right: auto;
  margin-left: auto;
  @media all and(min-width: 768px) {
    width: 450px;
    height: 45px;
    font-size: 26px;
    left: 50vw;
    transform: translate (-50%, 0%);
  }
  @media all and(min-width: 1200px) {
    font_size: 32px;
  }
}

.main__article--container {
  box-sizing: border-box;
  display: block;
  text-align: center;
}

.main__section--subtitle {
  font-family: "RobotoSlab-Bold";
  font-weight: 800;
  font-size: 18px;
  color: #000;
  margin-top: 10px;
  text-align: center;
  @media all and(min-width: 768px) {
    width: 210px;
    height: 30px;
    font-size: 24px;
  }
}
.main__section--paragraph {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 18px;
  color: #000;
  margin-top: 10px;
  text-align: center;
  @media all and(min-width: 768px) {
    width: 212.3px;
    height: 68px;
    font-size: 20px;
  }
}

.main__button--more-reasons {
  content: "More reasons";
  padding: 10px 30px;
  background-color: #f26002;
  font-family: "Roboto", sans-serif;
  font-size: 20px;
  color: #ffff;
  position: absolute;
  margin-left: 25%;
  border: 2px solid #f26002;
  border-radius: 30px;
  transition-duration: 1s;
  transition-timing-function: ease;
  transition-delay: 0s;
  white-space: nowrap;
  @media all and(min-width: 1200px) {
    margin-left: 42%;
  }
  @media all and(min-width: 768px) {
    margin-left: 37%;
  }
}

.main__button--more-reasons:hover {
  content: "More reasons";
  padding: 10px 30px;
  background-color: #ffff;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 20px;
  position: absolute;
  margin-left: 25%;
  color: #f26002;
  border: 2px solid #f26002;
  border-radius: 30px;
  transition-duration: 1s;
  transition-timing-function: ease;
  transition-delay: 0s;
  white-space: nowrap;
  @media all and(min-width: 768px) {
    margin-left: 37%;
  }
  @media all and(min-width: 1200px) {
    margin-left: 42%;
  }
}

.footer__scroll {
  width: 100vw;
  height: 0%;
  transform: translate(0, -100px);
  padding: 0 0 0 0;
  line-height: 0;
  font-size: 0;
  display: inline;
  margin-top: -20px;
}
.footer__buttom {
  border-radius: 50%;
  background-color: #fff;
  border: 0.5px;
  box-shadow: 0 2px 4px 0 black;
  position: absolute;
  padding: 12px 12px 8px 12px;
  left: 46vw;
  line-height: 0;
  font-size: 0;
  margin-top: -20px;
  animation: jump 0.7s ease 1s infinite alternate;
  @media all and(min-width: 1200px) {
    left: 47.5vw;
  }
}
@keyframes jump {
  0% {
    transform: scale(1.1, 0.9);
  }
  50% {
    transform: scale(0.9, 1.1) translateY(-8px);
  }
  70% {
    transform: scale(1);
  }
}
